* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: Arial, Helvetica, sans-serif;
	line-height: 1.4;
}

a {
	text-decoration: none;
	color: #000;
}

.container {
	max-width: 550px;
	margin: 0 auto;
	border: 2px solid #000;
	border-radius: 10px;
	overflow: hidden;
}

.addNewTask__form {
	display: -ms-flexbox;
	display: flex;
	margin: 8px;
}

.addNewTask__input {
	-ms-flex: 10 1;
	    flex: 10 1;
	padding: 15px;
	background: #c0c0c0;
	border: none;
	border-top-left-radius: 15px;
	border-bottom-left-radius: 15px;
	outline: none;
}

.addNewTask__input::-webkit-input-placeholder {
	color: #fff;
}

.addNewTask__input::-moz-placeholder {
	color: #fff;
}

.addNewTask__input::-ms-input-placeholder {
	color: #fff;
}

.addNewTask__input::placeholder {
	color: #fff;
}

.addNewTask__btn {
	-ms-flex: 2 1;
	    flex: 2 1;
	padding: 5px;
	background: #3f51b5;
	border: none;
	border-top-right-radius: 15px;
	border-bottom-right-radius: 15px;
	color: #fff;
}

.addNewTask__btn:hover {
	background: #7cf000;
	cursor: pointer;
}

.todoItem__wrapper {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding: 10px;
	background: #ccc;
}

.todoItem__title {
	-ms-flex: 7 1;
	    flex: 7 1;
	margin-left: 10px;
}

.todoItem__btn {
	background: #f00;
	padding: 5px;
	border: none;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
}

.todoItem__btn:hover {
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}



